.style{
    position: absolute;
    background: #70E5CF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
    z-index: 100000;
    height: 50%;
    width: 50%;
}
.text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    margin-top: 100px;
}
.close{
    position: absolute;
    top:20px;
    right:20px;
    cursor: pointer;
}
.close:active{
    top:23px;
    right:20px;
}